<template>
  <div class="w-calendar-page">
    <v-container fluid class="px-0 pt-0">
      <v-row no-gutters>
        <v-col cols="12" md="8">
          <div class="section-1 scroll-section">
            <v-toolbar flat dense height="72" color="skej-grey-background">
              <v-btn default :to="{ name: 'calendar' }"><v-icon>mdi-arrow-left</v-icon></v-btn>
              <v-spacer/>
              <v-btn fab text small color="grey darken-2" @click="prev">
                <v-icon small>mdi-chevron-left</v-icon>
              </v-btn>
              <v-toolbar-title><h3 class="font-weight-medium">{{ title }}</h3></v-toolbar-title>
              <v-btn fab text small color="grey darken-2" @click="next">
                <v-icon small>mdi-chevron-right</v-icon>
              </v-btn>
              <v-spacer/>
              <div id="color-bar">
                <div
                  :class="[
                  'event-color', event_colors['type1'],
                  active_types.includes('type1') ? '' : 'inactive'
                  ]"
                  @click="toggleEventTypeActive('type1')"
                ></div>
                <div
                  :class="[
                  'event-color', event_colors['type2'],
                  active_types.includes('type2') ? '' : 'inactive'
                  ]"
                @click="toggleEventTypeActive('type2')"
                ></div>
                <div
                  :class="[
                  'event-color', event_colors['type3'],
                  active_types.includes('type3') ? '' : 'inactive'
                  ]"
                  @click="toggleEventTypeActive('type3')"
                ></div>
                <div
                  :class="[
                  'event-color', event_colors['type4'],
                  active_types.includes('type4') ? '' : 'inactive'
                  ]"
                  @click="toggleEventTypeActive('type4')"
                ></div>
              </div>
            </v-toolbar>
            <v-divider></v-divider>
            <v-row no-gutters>
              <v-col cols="1">
                <v-list class="mt-12 pt-8"
                  flat
                  disabled
                  single-line
                  >
                  <div  v-for="hour in hours" :key="`hour-${hour.hr}`">
                    <v-list-item v-if="hour.hr >= day_start" class="px-0">
                      <v-card flat tile>
                        <v-card-text
                          class="font-weight-bold py-0 pr-0">
                          <div class="hrstart pr-4">
                            {{hour.hr}}:00
                          </div>
                          <div class="hr15 text--disabled">
                            15
                          </div>
                          <div class="hr30 text--disabled">
                            30
                          </div>
                          <div class="hr45 text--disabled">
                            45
                          </div>
                        </v-card-text>
                      </v-card>
                    </v-list-item>
                  </div>
                </v-list>
              </v-col>
              <v-col cols="11">
                <v-calendar
                  ref="week_calendar"
                  :type="'week'"
                  v-model="focus"
                  :locale="$i18n.locale + '-' + localStorage.country"
                  :event-more="false"
                  :interval-height="64"
                  :first-interval="day_start"
                  @change="updateRange"
                >
                <template v-slot:interval="{ date, hour }">
                  <div v-if="(hour <= 24) && (hour >= day_start)" class="hour-interval">
                    <div
                      v-for="(interval, i) in getInterval(date, hour)"
                      :key="`interval-${i}-${date}-${hour}`"
                    >
                      <div :class="['quarter-hour', 'pl-1',
                          ((
                          (interval.label_hour === hour) &&
                          (interval.label_quarter === 'start'))) ? '' : 'transparent-text']"
                        >
                        <div :class="['vertical-dash', (interval.quarters.includes('start')? event_colors[interval.type] : '')]"></div>
                        <span class="pl-1" v-if="active_types.includes(interval.type)">{{interval.name}}</span>
                      </div>
                      <div :class="['quarter-hour', 'pl-1',
                          ((
                          (interval.label_hour === hour) &&
                          (interval.label_quarter === 'quarter'))) ? '' : 'transparent-text']"
                        >
                        <div :class="['vertical-dash', (interval.quarters.includes('quarter')? event_colors[interval.type] : '')]"></div>
                        <span class="pl-1" v-if="active_types.includes(interval.type)">{{interval.name}}</span>
                      </div>
                      <div :class="['quarter-hour', 'pl-1',
                          ((
                          (interval.label_hour === hour) &&
                          (interval.label_quarter === 'half'))) ? '' : 'transparent-text']"
                        >
                        <div :class="['vertical-dash', (interval.quarters.includes('half')? event_colors[interval.type] : '')]"></div>
                        <span class="pl-1" v-if="active_types.includes(interval.type)">{{interval.name}}</span>
                      </div>
                      <div :class="['quarter-hour', 'pl-1',
                          ((
                          (interval.label_hour === hour) &&
                          (interval.label_quarter === 'quarter_til'))) ? '' : 'transparent-text']"
                        >
                        <div :class="['vertical-dash', (interval.quarters.includes('quarter_til')? event_colors[interval.type] : '')]"></div>
                        <span class="pl-1" v-if="active_types.includes(interval.type)">{{interval.name}}</span>
                      </div>
                    </div>
                  </div>
                </template>
                </v-calendar>
              </v-col>
            </v-row>
          </div>
        </v-col>
        <v-col cols="12" md="4">
          <div class="section-2 scroll-section">
            <v-toolbar
              flat
              class="group-toolbar skej-grey-background flex-none"
              height="72"
              >
              <v-toolbar-title class="ml-2">
                <h3 class="font-weight-medium">Upcoming Events</h3>
              </v-toolbar-title>
            </v-toolbar>
            <v-divider></v-divider>
            <div class="pt-2 pl-2 pt-lg-8 pl-lg-12">
              <div v-for="(event, i) in upcomingEvents"
                class="d-flex pr-lg-7 my-2"
                :key="`upcoming${i}`"
                style="height: 55px;"
              >
                <div :class="['vertical-dash', 'mx-1', event_colors[event.type]]"></div>
                <v-card flat tile
                  class="mr-4 my-2 d-inline skej-grey-background"
                >
                  <v-card-text
                    class="font-weight-medium px-1 py-2">
                    <span v-if="active_types.includes(event.type)">{{event.name}}</span>
                  </v-card-text>
                </v-card>
                <v-spacer></v-spacer>
                <v-card flat tile class="d-inline skej-grey-background my-2">
                  <v-card-text class="px-1 py-2 text--disabled">{{event.start}} | {{event.end}}</v-card-text>
                </v-card>
              </div>
            </div>
          </div>
        </v-col>
      </v-row>
    </v-container>
    <new-items-widget></new-items-widget>
  </div>
</template>

